<template>
  <div>
    <!-- <FormItem v-if="!value.hideFiledName" label="">{{value.filedName}}</FormItem> -->
    <FormItem label="" v-model="value.valueType" type="select" :options="valueTypeOptions"> </FormItem>
    <FormItem label="" v-model="value.formItemDefaultProps.required" type="switch"> </FormItem>
    <FormItem label="" v-model="value.config.validate.requiredValidateMsg" type="input"> </FormItem>
    <FormItem label="min" v-model="value.config.validate.min" type="number" tip=""> </FormItem>
    <FormItem label="min" v-model="value.config.validate.minValidateMsg" type="input"> </FormItem>
    <FormItem label="max" v-model="value.config.validate.max" type="number" tip=""> </FormItem>
    <FormItem label="max" v-model="value.config.validate.maxValidateMsg" type="input"> </FormItem>

    <FormItem label="">
      <el-button size="mini" style="width: 100%" icon="el-icon-edit" @click="rulesEditorDialogVisible = true">{{ value.config.validate.rules.length }}</el-button>
    </FormItem>
    <FormItem label="" v-model="value.config.validate.disabled" type="switch"> </FormItem>

    <el-dialog append-to-body :close-on-click-modal="false" :title="`(${value.config.validate.rules.length})`" :visible.sync="rulesEditorDialogVisible">
      <Form class="rules-editor" :label-length="0" margin-bottom="0">
        <el-table class="table" size="mini" :data="value.config.validate.rules" :max-height="350">
          <el-table-column label="">
            <template #default="{ row }">
              <FormItem type="select" v-model="row.key" :options="validateRules"></FormItem>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template #default="{ row }">
              <FormItem tip="">
                <el-autocomplete style="width: 100%" v-model="row.validateMsg" size="mini" :fetch-suggestions="querySearch" clearable>
                  <template #default="{ item }">
                    <span>{{ item.value }}</span>
                  </template>
                </el-autocomplete>
              </FormItem>
            </template>
          </el-table-column>
          <el-table-column label="" width="100">
            <template #default="{ row, $index }">
              <el-button size="mini" type="text" @click="value.config.validate.rules.splice($index, 1)"></el-button>
            </template>
          </el-table-column>
        </el-table>
        <div class="add-btn">
          <el-button icon="el-icon-plus" size="mini" @click="addValidateRule"></el-button>
        </div>
      </Form>

      <div style="text-align: right">
        <el-button size="mini" @click="rulesEditorDialogVisible = false"></el-button>
        <el-button size="mini" type="primary" @click="rulesEditorDialogVisible = false"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { Form, FormItem } from '../form'
import { valueTypes } from '../../utils/valueTypes'
import { validateRegExps } from '../../utils/validateRegExps'

export default {
  components: { Form, FormItem },
  props: {
    value: {},
    materials: {},
  },
  data() {
    return {
      rulesEditorDialogVisible: false,
      validateModeOptions: [{ label: '', value: 'and' }, { label: '' }],
    }
  },
  computed: {
    valueTypeOptions() {
      return Object.values(valueTypes)
    },
    validateRules() {
      return Object.values(validateRegExps)
    },
  },
  methods: {
    validateRuleChange(key) {
      const item = this.validateRules.find((item) => item.value === key)
    },
    addValidateRule() {
      this.value.config.validate.rules.push({ key: '', validateMsg: '' })
    },
    querySearch(queryString, cb) {
      const options = (this.validateRules || []).map((item) => ({ value: `${item.label}` }))
      const text = queryString.toLowerCase()
      const results = queryString
        ? options.filter((item) => {
            const value = item.value.toLowerCase()
            return value.indexOf(text) > -1
          })
        : options
      cb(results)
    },
  },
}
</script>

<style scoped>
.rules-editor .table {
  border: 1px solid #ebeef5;
  border-bottom: none;
  margin-bottom: 20px;
}
</style>
