<template>
  <div>
    <!--  -->
    <FormItem label="">{{ value.name }}</FormItem>
    <template>
      <FormItem label="" type="input" v-model="value.formItemDefaultProps.label"></FormItem>
      <FormItem label="" type="select" v-model="value.formItemDefaultProps.labelPosition" :options="labelPositionOptions"></FormItem>
      <FormItem label="" type="select" v-model="value.formItemDefaultProps.alignItems" :options="labelAlignItemsOptions"></FormItem>
      <FormItem label="" type="switch" v-model="value.formItemDefaultProps.labelVisible"></FormItem>
    </template>

    <FormItem label="">
      <el-button style="width: 100%" @click="defaultValueDialogVisible = true" size="mini" icon="el-icon-edit"></el-button>
    </FormItem>
    <FormItem v-if="Object.keys(value.props).length" label="" align-items="flex-start">
      <template v-for="(item, key) in value.props">
        <div style="margin-bottom: 10px;">
          <el-button style="width: 100%; magirn-botton: 10px" @click="showPropsItemEditor(item)" size="mini" icon="el-icon-edit">{{ item.label }}</el-button>
        </div>
      </template>
    </FormItem>
    <FormItem label="" type="switch" v-model="value.config.hidden" tip=""></FormItem>

    <el-dialog v-if="value" append-to-body :close-on-click-modal="false" :title="`${value.formItemDefaultProps.label}-`" :visible.sync="defaultValueDialogVisible" destroy-on-close>
      <Form>
        <FormItem label="" v-model="value.config.base.defaultValue.inputMode" type="select" :options="defaultValueInputOptions"> </FormItem>
        <div v-dimple-overlay style="max-height: 50vh">
          <template v-if="value.config.base.defaultValue.inputMode === 'default'">
            <Render v-if="defaultValueDialogVisible" v-model="value" :materials="materials" :append-props="value.defaultInputProps || {}" />
          </template>

          <template v-if="value.config.base.defaultValue.inputMode === 'input'">
            <FormItem label="" type="input" v-model="value.value"></FormItem>
          </template>

          <template v-if="value.config.base.defaultValue.inputMode === 'urlParam'">
            <Form>
              <FormItem label="" type="autocomplete" tip="" v-model="value.config.base.defaultValue.urlParamName" :options="urlParamNameOptions"> </FormItem>
              <FormItem label="" tip=""> {{ getQueryByKey(value.config.base.defaultValue.urlParamName) || '-' }} </FormItem>
            </Form>
          </template>
        </div>
      </Form>
      <div style="text-align: right">
        <el-button size="mini" @click="defaultValueDialogVisible = false"></el-button>
        <el-button size="mini" type="primary" @click="defaultValueDialogVisible = false"></el-button>
      </div>
    </el-dialog>
    <el-dialog v-if="value" append-to-body :close-on-click-modal="false" :title="`${value.formItemDefaultProps.label}-${currentPropsItem && currentPropsItem.label}-`" :visible.sync="propsItemEditorDialogVisible" destroy-on-close>
      <Form v-if="currentPropsItem">
        <template v-if="currentPropsItem.editType === 'custom'">
          <Render v-if="propsItemEditorDialogVisible" v-model="currentPropsItem" :materials="materials" />
        </template>
        <template v-else>
          <OptionsEditor v-if="currentPropsItem.editType === 'options'" v-model="currentPropsItem" />
          <FormItem v-else :label-length="0" :type="currentPropsItem.editType" v-model="currentPropsItem.value" v-bind="currentPropsItem.defaultProps || {}"></FormItem>
        </template>
      </Form>
      <div style="text-align: right">
        <el-button size="mini" @click="propsItemEditorDialogVisible = false"></el-button>
        <el-button size="mini" type="primary" @click="propsItemEditorDialogVisible = false"></el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { FormItem } from '../form'
import { Render } from '../render'
import OptionsEditor from '../options-editor/OptionsEditor.vue'
import { getQueryByKey } from '../../utils/getQueryByKey'
import { overlay } from '../../../../overlay';

export default {
  components: { FormItem, Render, OptionsEditor },
  props: {
    value: {},
    materials: {},
  },
  data() {
    return {
      labelPositionOptions: [
        { value: 'left', label: '' },
        { value: 'right', label: '' },
      ],
      labelAlignItemsOptions: [
        { value: 'center', label: '' },
        { value: 'flex-start', label: '' },
        { value: 'flex-end', label: '' },
      ],
      defaultValueInputOptions: [
        { value: 'default', label: '' },
        { value: 'input', label: '' },
        { value: 'urlParam', label: '' },
      ],
      urlParamNameOptions: [
        { value: 'mobile', label: 'mobile' },
        { value: 'username', label: 'username' },
        { value: 'projectId', label: 'projectIdid' },
        { value: 'projectName', label: 'projectName' },
        { value: 'projectCode', label: 'projectCode' },
      ],
      defaultValueDialogVisible: false,
      propsItemEditorDialogVisible: false,
      currentPropsItem: null,
    }
  },
  directives: {
    'dimple-overlay': overlay,
  },
  methods: {
    showPropsItemEditor(item) {
      this.propsItemEditorDialogVisible = true
      this.currentPropsItem = item
    },
    getQueryByKey,
  },
}
</script>

<style scoped></style>
